  #MainSec{ 
    
    color: #060605;
    text-align: center;
    margin-top: 2.5%;
    display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: repeat(1, 1fr);
  font-size: 2.1rem;
  /* font-style: italic; */
  margin-left: 12%;
  margin-right: 12%;
 }
 #MainSec p{
  text-shadow: 1px 1px rgb(0, 0, 0);
  margin-left: 8%;
  margin-right: 8%;
  font-weight: 200;
  /* font-weight: 600; */
 }
 
 #MainSec li {
  text-shadow: 1px 1px black;
  font-weight: 200;
  margin-left: 5px;
  display: inline-block;
  }

  #menu a:hover{
   color: #f1f2f7; 
   
 }  
 
#menu{
  margin-left: 15%;
  margin-right: 15%;
}

#menu ul,li, a{

  font-weight: bolder;
  font-size: 2.2rem;
  color: inherit; 
  width: 80%;
  width: auto;
  display: flex;
  flex-direction: row;
  list-style: none;
  text-decoration-line: none;
  text-decoration: unset;
}

#menu li{
  margin-left: 15%;
  margin-right: 8%;
}

  
@media  only screen and  (min-width: 240px)  and (max-width: 700px){

  body{
    margin: 0;
    position: absolute;
    justify-content: center;
    text-align: center;
    /* margin-left: 10%; */
    font-size: clamp(1.5rem, 0rem, 5rem);
   }
  
   #Mainsec{
     position: relative;
   }
  
   #MainSec h1,h2{
     font-size: 2rem;
     margin-bottom: 12%;
   }
   #MainSec p{
     margin-bottom: 10%;
   }
   #MainSec img{
    margin-bottom: 5%;
  }
  #information{
    width: auto;
  }


  #menu{
    position: relative;
    width: 40%;
    /* display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  grid-template-rows: 1fr; */
     font-size: 1.5rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  grid-template-rows: 1fr;
  grid-gap: 10px;
  text-align: center;
  justify-content: center;
  align-content: center;

  }
  /* #menu li{
    margin-left: 3%;
  } */
  
} 
